import React from 'react';
import {
  Box,
  TextField,
  Button,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Grid,
  IconButton,
  Typography,
} from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import DeleteIcon from '@mui/icons-material/Delete';

const questionTypes = [
  { value: 'text', label: 'Text Input' },
  { value: 'radio', label: 'Multiple Choice' },
];

function FormBuilder({ questions = [], onUpdate }) {
  const handleAddQuestion = () => {
    onUpdate([
      ...questions,
      {
        type: '',
        label: '',
        options: [],
        required: true,
      },
    ]);
  };

  const handleQuestionChange = (index, field, value) => {
    const newQuestions = [...questions];
    newQuestions[index] = {
      ...newQuestions[index],
      [field]: value,
    };
    
    // Initialize empty options array when switching to radio type
    if (field === 'type' && value === 'radio') {
      newQuestions[index].options = [];
    }
    
    onUpdate(newQuestions);
  };

  const handleAddOption = (questionIndex) => {
    const newQuestions = [...questions];
    newQuestions[questionIndex].options = [
      ...(newQuestions[questionIndex].options || []),
      { label: '', price: '' },
    ];
    onUpdate(newQuestions);
  };

  const handleOptionChange = (questionIndex, optionIndex, field, value) => {
    const newQuestions = [...questions];
    newQuestions[questionIndex].options[optionIndex] = {
      ...newQuestions[questionIndex].options[optionIndex],
      [field]: value,
    };
    onUpdate(newQuestions);
  };

  const handleDeleteOption = (questionIndex, optionIndex) => {
    const newQuestions = [...questions];
    newQuestions[questionIndex].options.splice(optionIndex, 1);
    onUpdate(newQuestions);
  };

  const handleDeleteQuestion = (index) => {
    const newQuestions = questions.filter((_, i) => i !== index);
    onUpdate(newQuestions);
  };

  return (
    <Box>
      <Typography variant="h6" gutterBottom>
        Form Builder
      </Typography>
      
      {questions.map((question, questionIndex) => (
        <Box key={questionIndex} sx={{ mb: 4, p: 2, border: '1px solid #e0e0e0', borderRadius: 1 }}>
          <Grid container spacing={2} alignItems="flex-start">
            <Grid item xs={11}>
              <Grid container spacing={2}>
                <Grid item xs={12} md={6}>
                  <FormControl fullWidth>
                    <InputLabel>Question Type</InputLabel>
                    <Select
                      value={question.type}
                      label="Question Type"
                      onChange={(e) => handleQuestionChange(questionIndex, 'type', e.target.value)}
                    >
                      <MenuItem value="">
                        <em>Select a type</em>
                      </MenuItem>
                      {questionTypes.map((type) => (
                        <MenuItem key={type.value} value={type.value}>
                          {type.label}
                        </MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    fullWidth
                    label="Question"
                    value={question.label}
                    onChange={(e) => handleQuestionChange(questionIndex, 'label', e.target.value)}
                  />
                </Grid>
              </Grid>

              {question.type === 'radio' && (
                <Box sx={{ mt: 2 }}>
                  <Typography variant="subtitle2" gutterBottom>
                    Options
                  </Typography>
                  {question.options?.map((option, optionIndex) => (
                    <Grid container spacing={2} key={optionIndex} sx={{ mb: 1 }}>
                      <Grid item xs={6}>
                        <TextField
                          fullWidth
                          label="Option Text"
                          value={option.label}
                          onChange={(e) => handleOptionChange(questionIndex, optionIndex, 'label', e.target.value)}
                        />
                      </Grid>
                      <Grid item xs={5}>
                        <TextField
                          fullWidth
                          label="Price"
                          type="number"
                          value={option.price}
                          onChange={(e) => handleOptionChange(questionIndex, optionIndex, 'price', e.target.value)}
                          InputProps={{
                            startAdornment: '$'
                          }}
                        />
                      </Grid>
                      <Grid item xs={1}>
                        <IconButton 
                          onClick={() => handleDeleteOption(questionIndex, optionIndex)}
                          color="error"
                        >
                          <DeleteIcon />
                        </IconButton>
                      </Grid>
                    </Grid>
                  ))}
                  <Button
                    startIcon={<AddIcon />}
                    onClick={() => handleAddOption(questionIndex)}
                    sx={{ mt: 1 }}
                  >
                    Add Option
                  </Button>
                </Box>
              )}
            </Grid>
            <Grid item xs={1}>
              <IconButton 
                onClick={() => handleDeleteQuestion(questionIndex)}
                color="error"
              >
                <DeleteIcon />
              </IconButton>
            </Grid>
          </Grid>
        </Box>
      ))}

      <Button
        variant="contained"
        startIcon={<AddIcon />}
        onClick={handleAddQuestion}
      >
        Add Question
      </Button>
    </Box>
  );
}

export default FormBuilder;
